<template>
  <div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">
              {{ $t("Comment") }}
            </h5>
            <button v-on:click="CloseCommentPopup($event)" href="javascript:;" class="close">
              <i class="fa fa-times" aria-hidden="true"></i>
            </button>
          </div>
          <div class="modal-body">
            <div class="popupscroller modal-body" style="height: auto; min-height: 0px; max-height: none">
              <div class="form-content">
                <div class="collapse show py-0">
                  <div class="card-body comments">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                      <div class="panel panel-white post panel-shadow">
                        <div class="post-heading">
                          <div class="pull-left image">
                           <img src="/Content/images/DefaultImages/avatar-icon.jpg"
                              class="img-circle avatar pr-5" alt="Image" />
                          </div>
                          <div class="" style="padding: 10px">
                            <div class="title h6">
                              <b>{{ commentData.TITLE }}</b>
                            </div>
                            <span class="text-muted time">
                              <i>
                                <b>{{ commentData.USERNAME }}</b>, {{ $t('Postedon') }}
                                <span class="text-danger">{{
                                  $options.filters.formatDateTime(
                                    commentData.RETURN_DATE
                                  )
                                }}</span>
                              </i>
                            </span>
                            <div class="post-description" style="width: 85%">
                              <p title=""><br /></p>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import DataService from "../../service/DataService";

export default {
  props: {
    assDetailIDs: {
      type: Number,
      required: true,
    },
  },

  data() {
    return {
      commentData: [],
    };
  },
  created() {
    this.GetCommentUserDetail();
  },
  methods: {
    CloseCommentPopup: function (item) {
      this.$emit("CloseCommentPopup", item);
    },

    async GetCommentUserDetail() {
      const url = `assignmentId=${this.assDetailIDs}`;
      await DataService.GetAssetReturnComment(url).then((response) => {
        if (response.data != null) {
          debugger
          this.commentData = response.data.DATA[0];
          console.log("comment", this.commentData);
        } else {
          this.commentData = "";
        }
      });
    },
  },
};
</script>
