<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('Return') }}
                        </h5>

                        <button v-on:click="CloseNotReurnedProductPopup($event)" href="javascript:;" class="close"><i
                                class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <div class="border p-3">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                        <span class="mandatory"> {{ $t('FieldRequired') }}</span>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>




<script>

import DataService from '../../service/DataService';

export default {
    props: {
        selectedUserDetail: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            result: null,
            noRecordForTab: false,
            isLoading: false,
            AssignmentData: [],
            txtTitle: '',
            txtComment: '',
            UserId: null,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Title'),
                            name: "Title",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true }
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Number of Product'),
                            name: "NumberofProduct",
                            value: `${this.selectedUserDetail.NUMBER_OF_ITEMS_ASSIGNED ? this.selectedUserDetail.NUMBER_OF_ITEMS_ASSIGNED : 1}`,
                            placeholder: "",
                            disabled: this.selectedUserDetail.NUMBER_OF_ITEMS_ASSIGNED ? false : true

                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Comment'),
                            name: "Comment",
                            value: "",
                            placeholder: "",
                        }

                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save'),
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: this.$t('Skip') + "&nbsp;<i class='fa fa-fast-forward pr-2'></i>",
                    onClick: this.onSkip
                }
            ],

        }
    },
    created: function () {
        this.UserId = this.GetUserInfo.ID;

    },
    methods: {
        CloseNotReurnedProductPopup: function () {
            this.$emit('CloseNotReurnedProductPopup')
        },

        onSkip: function () {

            this.onSubmit();
            this.CloseNotReurnedProductPopup();
        },

        async onSubmit(formfields) {
            let vm = this;
            const obj = {
                // RequestID: this.selectedUserDetail.REQUEST_ID,
                RequestID: 0,
                AssetAssignmentDetailId: this.selectedUserDetail.ASSIGNMENT_DETAIL_ID,
                NumberOfItems: parseInt(formfields.NumberofProduct),
                UserId: this.selectedUserDetail.ISSUED_TO_ID,
                // RelatedInfoID: this.selectedUserDetail.RELATED_INFO_ID,
                RelatedInfoID: 0,
                Comment: formfields.Comment,
                Title: formfields.Title
            }


            let jsonObject = JSON.stringify(obj);
            await DataService.UpdateAssetAssignmentStatus(jsonObject).then((response) => {
                if (response.data != null) {
                    debugger;
                    this.result = response.data.result;
                    if (this.result == true) {
                        this.CloseNotReurnedProductPopup();
                        this.$parent.FetchData();
                        return vm.ShowAlert(
                            vm.$t('Item has been return Successfully'),
                            "success",
                            vm.$t("Alert")
                        );
                    }
                    else if (this.result == false) {
                        if (response.data.msg == 'NotReturnedSuccessfully') {
                            this.CloseNotReurnedProductPopup();
                            this.$parent.FetchData();
                            return vm.ShowAlert(
                                vm.$t('Already Returned'),
                                "success",
                                vm.$t("Alert")
                            );
                        }

                    }
                    else {
                        this.CloseNotReurnedProductPopup();
                        this.$parent.FetchData();
                        vm.ShowAlert(
                            vm.$t('ErrorMessage'),
                            "Failure",
                            vm.$t("Alert")
                        );
                    }
                }

            });

        }
    },

}
</script>