<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('Request For Return') }}
                        </h5>
                        <button v-on:click="CloseRequestReturnPopup" type="button" class="close"
                            data-original-title="Close"><em class="fa fa-times"></em></button>

                    </div>
                    <div class="border p-3">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../service/DataService';
export default {
    props:{
        IsRequstForReturnData:{
            type:Array,
            required:true
        }
    },
    data() {
        return {
            IsShowAddNamePopup:false,
            isLoading:false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [                       
                        {
                            astype: "NumericField",
                            label:"Number of Products",
                            name: "NumberofProducts",
                            value: "",
                            placeholder: "",
                            disabled:true,
                            validationRules: { "required": true, "min_value": 1, max: 7 },
                        },
                        {
                               astype: "DateField",
                               label: "Return Until (YYYY-MM-DD)",
                               name: "ReturnUntil",
                               value: (new Date()),
                               placeholder: "",
                               validationRules: false,
                               config:{
                                    format: "YYYY-MM-DD",
                                    minDate: (new Date())                               
                                }    
                         },

                    ]
                },
                {
                    layoutType: "single",
                    Data: [                       
                        {
                            astype: "TextAreaField",
                            label: this.$t('Comment'),
                            name: "Comment",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false },
                            config: {
                                rows:5
                            }
                        },                      

                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseRequestReturnPopup
                }
            ]      
        }
    },
    created: function () {
        this.FetchRequestForReturn();
    },
    methods: { 
        FetchRequestForReturn:function(){           
            (this.FormSchema)[0].Data[0].value =this.IsRequstForReturnData.NUMBER_OF_ITEMS_ASSIGNED;
            (this.FormSchema)[0].Data[1].value =this.IsRequstForReturnData.ASSIGNED_TILL;            
        },
        GetPrivilege: function(params){ 
            debugger           
            // DataService.CheckPrivilege(params).then(function (response) {
            //         if (response.data) {
            //             debugger
            //         }
            //     });
            return true;
            },
            
        CloseRequestReturnPopup:function(){   
            this.$emit('CloseReturnPopup')
        },
        RequstForReturn: function (ths) {            
          this.IsRequstForReturnData = ths;
          this.IsRequstForReturn = true;
        },
        onSubmit : function(formfields, val){     
            var vm=this;
            vm.isLoading=true;
            if (vm.GetPrivilege('controller=AssetAssignment&action=AssetRequestForReturn')) {                
                let obj={
                    Comment:formfields.Comment,
                    RelatedInfoID:vm.IsRequstForReturnData.RELATED_INFO_ID,
                    NumberOfItems:vm.IsRequstForReturnData.NUMBER_OF_ITEMS_ASSIGNED,
                    ReturnUntil:formfields.ReturnUntil,
                    Status_Code:1001,
                    UserID:vm.IsRequstForReturnData.ISSUED_TO_ID,
                    AssetAssignmentDetailId:vm.IsRequstForReturnData.ASSIGNMENT_DETAIL_ID,
                    UniqueName:vm.IsRequstForReturnData.UNIQUE_NAME,
                    Status_Name:"Manager"
                }
                var JsonObj=JSON.stringify(obj);
                DataService.SaveRequestForReturn(JsonObj).then(response=>{
                    
                    if(response.data.result==1){
                        vm.ShowAlert(vm.$t("Request for Return saved successfully."), "success", vm.$t('Alert'));
                        vm.CloseRequestReturnPopup();
                        vm.isLoading=false;
                        vm.$parent.FetchData();
                    }else{
                        vm.isLoading=false;
                        vm.ShowAlert(vm.$t("Error while returning item."), "warning", vm.$t('Alert'));
                    }
                });
            }
            
        }
    }
}
</script>